$icon-font-path: "bootstrap-sass-3.2.0/assets/fonts/bootstrap/";
$footer_height: 3em;

@import "bootstrap-sass-3.2.0/assets/stylesheets/bootstrap";
@import "material-wfont";
// The SASS-Version of Material seems to be broken (Variants of Checkboxes are not included, for example, therfore we include the compiled LESS-Version instead)
@import "material/variables";
@import "antragsliste";
@import "fontello";

#content {
  > section {
    margin-top: 50px;
  }
}

#page_main_content {
  margin-bottom: 25px;
}

#main_navbar {
  .navbar-nav > li.so_funktioniert > a {
    /*
      padding-top: 5px;
      padding-bottom: 0;
    */
    br {
      display: none;
    }
  }

}

.well {
  > h1, > h2, > h3 {
    margin-top: 0;
  }
  > .breadcrumb:first-child {
    margin-bottom: 5px;
    margin-left: -18px;
    margin-right: -18px;
    margin-top: -18px;
  }
  > .alert {
    max-width: 600px;
    margin: 30px auto;
    .fontello-ok {
      float: left;
      font-size: 200%;
      margin-right: 10px;
    }
  }
}

#ba_nav_list {
  min-width: 270px;

  @media screen and (max-height: 710px) and (min-width: 600px) {
    -moz-column-count: 2;
    -moz-column-gap: 10px;
    -webkit-column-count: 2;
    -webkit-column-gap: 10px;
    column-count: 2;
    column-gap: 10px;

    > li > a {
      white-space: normal;
      width: 250px;
      text-indent: -25px;
      margin-left: 25px;
    }
  }

  .stadtrat > a {
    font-weight: bold;
  }
}

body {
  padding-top: 60px;
  background-image: url(/images/pattern-7nl1.png);
}

/* Set widths on the navbar form inputs since otherwise they're 100% wide */
.navbar-form input[type="text"],
.navbar-form input[type="password"] {
  width: 180px;
}

/* Wrapping element */
/* Set some basic padding to keep content from hitting the edges */
.body-content {
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: 768px) {
    padding: 0;
  }
}

.leaflet-popup-content-wrapper {
  border-radius: 10px;
}

.leaflet-popup .ort {
  float: right;
}

.leaflet-popup .ort_dokument {
  border-top: solid 1px #999;
  margin-top: 6px;
  padding-top: 6px;
}

.leaflet-marker-textmarker .text {
  position: absolute;
  z-index: 203;
  font-size: 13px;
  top: 14px;
  left: 0;
  width: 24px;
  text-align: center;
  color: white;
  font-weight: bold;
  text-shadow: 0 0 4px yellow;
  filter: dropshadow(color=yellow, offx=0, offy=0);
}

#mapholder {
  margin: 0;
  border-radius: 10px;
  overflow: hidden;
  padding: 6px;
}

#map {
  height: 420px;
}

#benachrichtigung_hinweis {
  border: solid 1px #ccc;
  background-color: #eee;
  font-size: 0.9em;
  border-radius: 5px;
  padding: 0.5em;
  margin: 0.4em;
  margin-top: -2em;
  text-align: center;
  opacity: 0;
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.15s ease;
  -moz-transition: height 0.15s ease;
  -ms-transition: height 0.15s ease;
  -o-transition: height 0.15s ease;
  transition: height 0.15s ease;

  &.half_visible {
    height: 6em;
    -webkit-transition: height 0.15s ease;
    -moz-transition: height 0.15s ease;
    -ms-transition: height 0.15s ease;
    -o-transition: height 0.15s ease;
    transition: height 0.15s ease;
  }

  &.half_visible_tmp1 {
    height: auto;
    min-height: 6em;
  }

  &.half_visible_tmp2 {
    height: 6em;
    -webkit-transition: opacity 0.35s ease;
    -moz-transition: opacity 0.35s ease;
    -ms-transition: opacity 0.35s ease;
    -o-transition: opacity 0.35s ease;
    transition: opacity 0.35s ease;
  }

  &.visible {
    height: auto;
    min-height: 6em;
    opacity: 1;
    -webkit-transition: opacity 0.35s ease;
    -moz-transition: opacity 0.35s ease;
    -ms-transition: opacity 0.35s ease;
    -o-transition: opacity 0.35s ease;
    transition: opacity 0.35s ease;
  }
}

.leaflet-control-zoom-fullscreen {
  background-image: url(/js/Leaflet.Fullscreen/icon-fullscreen.png);
}

.leaflet-container:-webkit-full-screen {
  width: 100% !important;
  height: 100% !important;
}

.map-overlay-info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;

  h4 {
    margin: 0 0 5px;
    color: #777;
  }
}

#listen_holder {

  &.nur_dokumente {
    .keine_dokumente {
      display: none;
    }
  }
  html.no-csscolumns &.nur_dokumente li {
    width: 49%;
    display: inline-block;
  }
}

section.teaser {
  a {
    text-align: left;
    display: block;
    overflow: auto;
    padding-right: 10px;

    h2 {
      margin: 0;
      padding: 5px;
      font-size: 19px;
      font-weight: bold;
      text-align: left;

      .glyphicon {
        font-weight: normal;
        margin-right: 10px;
        float: left;
        font-size: 30px;
        margin-left: -20px;
      }
    }

    .description {
      float: left;
    }
  }
}

.teaser_holder {
  > div {
    padding: 0px 20px 10px;

    > a {
      display: block;
      margin-bottom: 15px;
      border: solid 1px #333;
      background-color: #e4e4e4;
      -webkit-transition: background-color 0.3s;
      -moz-transition: background-color 0.3s;
      -o-transition: background-color 0.3s;
      transition: background-color 0.3s;
      border-radius: 5px;
      &:link, &:visited {
        color: black;
        text-decoration: none;
      }
      &:hover {
        background-color: #eeeeee;
        -webkit-transition: background-color 0.3s;
        -moz-transition: background-color 0.3s;
        -o-transition: background-color 0.3s;
        transition: background-color 0.3s;
      }

      h2 {
        margin: 0;
        padding: 5px;
        font-size: 19px;

        .glyphicon {
          font-weight: normal;
          margin-right: 10px;
        }
      }

      .description {
        border-top: solid 1px #333;
        padding: 5px;
      }
    }
  }
}

