.suchergebnisse_holder {
  position: relative;
  width: 100%;

  ul.suchergebnisliste {
    li {
      margin: 5px;
      width: 49%;
      vertical-align: top;
      padding: 0;
      overflow: hidden;
      height: 117px;

      .datum {
        float: right;
        color: #777;
        font-size: 10px;
        padding-top: 10px;
      }
      .dokument {
        display: block;
        background-color: #ddd;
        padding: 2px;
        font-weight: bold;
      }
      .antraglink {
        padding: 2px;
        font-size: 12px;
        line-height: 16px;
        height: 35px;
        overflow: hidden;
        display: block;
        position: relative;

        &:after {
          content: ' ';
          position: absolute;
          top: 21px;
          right: 0;
          height: 21px;
          width: 50px;

          /* IE9 SVG, needs conditional override of 'filter' to 'none' */
          background: url(data:image/svg+xml;base64,);
          background: -moz-linear-gradient(left, rgba(238, 238, 238, 0) 0%, rgba(238, 238, 238, 1) 72%, rgba(238, 238, 238, 1) 100%); /* FF3.6+ */
          background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(238, 238, 238, 0)), color-stop(72%, rgba(238, 238, 238, 1)), color-stop(100%, rgba(238, 238, 238, 1))); /* Chrome,Safari4+ */
          background: -webkit-linear-gradient(left, rgba(238, 238, 238, 0) 0%, rgba(238, 238, 238, 1) 72%, rgba(238, 238, 238, 1) 100%); /* Chrome10+,Safari5.1+ */
          background: -o-linear-gradient(left, rgba(238, 238, 238, 0) 0%, rgba(238, 238, 238, 1) 72%, rgba(238, 238, 238, 1) 100%); /* Opera 11.10+ */
          background: -ms-linear-gradient(left, rgba(238, 238, 238, 0) 0%, rgba(238, 238, 238, 1) 72%, rgba(238, 238, 238, 1) 100%); /* IE10+ */
          background: linear-gradient(to right, rgba(238, 238, 238, 0) 0%, rgba(238, 238, 238, 1) 72%, rgba(238, 238, 238, 1) 100%); /* W3C */
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00eeeeee', endColorstr='#eeeeee', GradientType=1); /* IE6-8 */
        }
      }
      blockquote {
        background: #fff;
        height: 55px;
        font-size: 12px;
        padding: 2px;
        margin: 0;
        display: block;
        border: 0;
        border-top: solid 1px #ccc;
        border-left: none;
      }
    }
  }
}

