/* keep the footer at the bottom of the page */
body, html {
  height: 100%;
}

.over_footer_wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%;
  margin: 0 auto (-1 * $footer_height);
}

footer {
  height: $footer_height;
  background-color: $navbar-default-bg;
  a {
    &:link, &:visited {
      color: $navbar-color;
    }
  }
  p {
    height: $footer_height;
    line-height: $footer_height;
    vert-align: middle;
    margin-bottom: 0;
    margin-top: 0;
  }
}

.footer_spacer {
